import Bmob from "hydrogen-js-sdk";
export default {
	//
	send_in(params){
		let openid = uni.getStorageSync("setting").wx_openid
		uni.request({
		    url: 'https://www.jimuzhou.com/api/send_temp.php', //
			method:"POST",
		    data: {
		        openid: openid,
				frist:params.frist,
				data1: params.data1,
				data2: params.data2,
				remark: params.remark,
				url:params.url,
				type:"in"
		    },
		    header: {
		        'content-type': 'application/x-www-form-urlencoded' //
		    },
		    success: (res) => {
		        console.log(res);
		    }
		});
	},
	
	//
	send_in_mini(params){
		let modelData = {
		    "touser": uni.getStorageSync("openid"),
		    "template_id": "nqZl9FWDofCo9cxU4de45grLKh2GU-PJcAvWSbJMy9k",
		    "page": "/pages/report/EnteringHistory/detail/detail?id="+params.id,
		    "form_id":params.form_Id,
		    "data": params,
		    "emphasis_keyword": ""
		}
		
		Bmob.sendWeAppMessage(modelData).then(function (response) {
		    console.log(response);
		}).catch(function (error) {
		    console.log(error);
		});
	},
	
	//
	send_out_mini(params){
		let modelData = {
		    "touser": uni.getStorageSync("openid"),
		    "template_id": "z0Km0FFiuvnNiUcXtZD046j43ue5_AxR05cDHFLLXPY",
		    "page": "/pages/report/EnteringHistory/detail/detail?id="+params.id,
		    "form_id":params.form_Id,
		    "data": params,
		    "emphasis_keyword": ""
		}
		
		Bmob.sendWeAppMessage(modelData).then(function (response) {
		    console.log(response);
		}).catch(function (error) {
		    console.log(error);
		});
	},
	
	//
	send_temp(params){
		let openid = uni.getStorageSync("setting").wx_openid
		uni.request({
		    url: 'https://www.jimuzhou.com/api/send_temp.php', //
			method:"POST",
		    data: {
		        openid: openid,
				data1: params.data1,
				data2: params.data2,
				data3: params.data3,
				data4: params.data4,
				remark: ":"+params.remark,
				url:params.url,
				type:"out"
		    },
		    header: {
		        'content-type': 'application/x-www-form-urlencoded' //
		    },
		    success: (res) => {
		        console.log(res);
		    }
		});
	}
}
